import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { Grid } from "@material-ui/core"
import "../styles/AppStyle.css"
import { ResumeData } from "../data/ResumeData";
import BackgroundImage from 'gatsby-background-image'

export default function AboutPage() {

	const data = useStaticQuery(graphql`
    query {
      image1: file(relativePath: { eq: "tacos.jpg" }) {
        childImageSharp {
          fluid(quality: 90) {
            ...GatsbyImageSharpFluid
          }
        }
      }
	  image2: file(relativePath: { eq: "mt_elbert.jpg" }) {
        childImageSharp {
          fluid(quality: 90, maxWidth: 1920) {
            ...GatsbyImageSharpFluid
          }
        }
      }
	  image3: file(relativePath: { eq: "havasu.jpg" }) {
        childImageSharp {
          fluid(quality: 90) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)


	return(
		<Layout>
			<SEO title="About Me" />
			<BackgroundImage
				Tag="section"
				fluid={data.image2.childImageSharp.fluid}
				backgroundColor={`#040e18`}
				style={{
					backgroundPosition: 'top center',
					backgroundRepeat: 'none',
					backgroundSize: 'cover',
					minHeight: '430px',
					height: '70vh',
					marginBottom: 60,
					position: 'relative'
				}}
			>
				<div style={{ padding: 20, color: 'white', backgroundColor: 'rgba(0,0,0,0.3)', position: 'absolute', top: '20%', left: '3%', borderRadius: 15}}>
					<ul style={{listStyle: 'none'}}>
						<li style={{ opacity: 0.8 }}>Programmer</li>
						<li style={{ opacity: 0.8 }}>Writer</li>
						<li style={{ opacity: 0.8 }}>Conservationist</li>
						<li style={{ opacity: 0.8 }}>Athlete</li>
						<li style={{ opacity: 0.8 }}>Engineer</li>
						<li style={{ opacity: 0.8 }}>Adventurer</li>
					</ul>
					<div style={{
						marginLeft: '1.45rem',
						marginTop: '2rem',
						fontWeight: 550,
						fontSize: '16pt'
					}}>
						My name is Bryce.
					</div>
				</div>
			</BackgroundImage>
			
			<Grid container spacing={2}>
				<Grid item md={4} xs={12}>
					<div style={{ marginBottom: `1.45rem` }}>
						<Img fluid={data.image1.childImageSharp.fluid} />
					</div>
				</Grid>
				<Grid item md={8} xs={12}>
					<div>
						I asked a former software engineer how someone with the skills suited for Silicon Valley could help with crucial
						problems like climate change, conservation, and sustainability.  His answer? <i>"Quit the industry and start a farm"</i>, which is exactly what he had done years before.
					</div>
					<div style={{marginTop: 20}}>
						That answer didn't suffice for me.
					</div>
					<div style={{marginTop: 20}}>
						I studied aerospace engineering at Purdue Univerity and moved into web and software development.  Learning to build rockets
						and web applications is awesome, but now I look to take these skills and find a bridge between using them in the digital frontier 
						and the natural world.  My college advisors would look up with puzzled expressions when I would drop a marine biology course
						into my engineering curriculum.  Likewise my biology professors had no clue why I was in their class.  But I've never been able
						to let go of my love for aquatic life, and now I hope to find a way to make that passion mean something.
					</div>
					<div style={{marginTop: 20}}>
						I wrote this little website in an effort to bring all my ideas into a single organized space, as well as 
						keeping me on task.  A place to just toss out some concepts, document their successes and/or failures, and provide
						a modicum of entertainment for the occasional visitor.
					</div>
				</Grid>
			</Grid>
			<div style={{marginTop: 40}}>

			
				<Grid container spacing={2}>
					
					<Grid item md={4} xs={12}>
						<div style={{ height: '100%', marginBottom: `1.45rem`, marginTop: 40 }}>
							<Img fluid={data.image3.childImageSharp.fluid} />
						</div>
					</Grid>
					<Grid item md={8} xs={12}>
						<div id="resume">
							<div id="title">Resume</div>
							{
								ResumeData.map(item => (
									<div className="resume-item">
										<h4>{item.role}</h4>
										<h5>{item.where}</h5>
										<ul>
											{
												item.details.map(detail => (
													<li>{detail}</li>
												))
											}
											</ul>
									</div>
								))
							}
						</div>
					</Grid>
				</Grid>
			</div>
		</Layout>

	)
}
